* {
  box-sizing: border-box;
}

body {
  position: relative;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  font-family: 'Open Sans';
  font-weight: 500;
  font-size: 60px;
  overflow: hidden;
}

.span__container {
  order: 1;
  width: 60vw;
  height: 100vh;
  line-height: 0.1rem;
  background-color: black;
}

.gear__container {
  position: relative;
  order: 0;
  width: 40vw;
  height: 100%;
  line-height: 0.1rem;
}

.gear__name__wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  padding: 60px 0px;
  transform: translateX(-1%);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  text-align: center;
  pointer-events: none;
  z-index: 2;
}

.gear__name {
  color: #d0d0d0;

  &--active {
    color: #ffffff;
  }
}

.gear__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  user-select: none;
  z-index: 0;
}

.handler {
  position: absolute;
  cursor: pointer;
  z-index: 1;
  transform: translate(56%, 21%);
  filter: drop-shadow(3px 5px 2px rgb(0 0 0 / 0.4));
}

.btnController {
  display: block;
  opacity: 0;

  &--active {
    display: block;
  }
}
